<template>
    <list ref="list" :options="options" @success="onSuccess" @scroll="onScroll">
		
	
        <view class="item" v-for="(item, index) in list" :key="index">
			
	    <view style="margin-left:20upx; ">
			<text style="font-size:small;">{{type}}</text>
		</view>
		<view>
			
		
		<text style="margin-left:20upx;">2020-06-08</text>
		
		<text style="float:right;text-align: right; ">惠州市长沙市十四街道中</text>
		</view>
</view>
		
		
		<view class="scroll-top" @click="$refs.list.scrollTop()" v-show="showToTop">回到顶部</view>
    </list>
</template>

<script>
	

 import {mapState} from 'vuex';
	
  export default {
	  data() {
		  return {
			  list: [],
			  showToTop: false
		  }
	  },
      computed: {
		...mapState(['forcedLogin', 'hasLogin', 'userName', 'workid', 'department', 'dtname','userId']),
	
          options() {
              return {
				  
				  url:this.LoginHost+"/api/Direction/List", //仅为示例，并非真实接口地址。
                  params: { 
						userId:this.userId,
						pageindex:1,
						pagesize:20,
						SigningKey :this.ApiKey
                  }
              }

          }

      },
	  methods: {
		onSuccess(list) {
			this.list = list
			console.log("请求成功，当前的列表内容为：", list)
		},
		onScroll(e) {
			const {scrollTop} = e.target
			if(scrollTop > 100) {	
				this.showToTop = true
			} else {
				this.showToTop = false
			}
		}
	  },
      mounted() {
          this.$refs.list.request()
      }
  }
</script>
<style>
	.item {
		padding: 10upx;
		margin: 5upx;
		border-bottom: 1px solid #E5E5E5;
		
		font-size: 30upx;
	}
	.scroll-top {
		position: fixed;
		right: 0;
		bottom: 0;
		content: url(data:image/png;base64,);
		width: 70upx;
		height: 70upx;
		padding: 20upx;
	}
</style>